import React from 'react';

const AboutUsScreen = () => {
  return (
    <div style={containerStyle} >
      <div style={contentStyle}>
        <h2 style={{
          fontSize: '30px',
          fontWeight: 'bold',
          color: 'red',
          textAlign: 'center',
        }}>About Us</h2>
        <p style={paragraphStyle}>
          Welcome to Pizza Palace  your ultimate destination for mouthwatering pizzas and
          delightful dining experiences. Since our establishment in 2021, we've been committed to
          delivering the finest quality pizzas straight to your doorstep.
        </p>
        <p style={paragraphStyle}>
          Our expert chefs use only the freshest ingredients and hand-crafted recipes to create the
          perfect pizza. From classic favorites to innovative flavors, we have something for every
          pizza lover.
        </p>
        <p style={paragraphStyle}>
          Our commitment to quality, taste, and customer satisfaction has made us a trusted name in
          the pizza industry. Join us in savoring the joy of a perfectly baked pizza today!
        </p>
      </div>
      <div style={imageContainerStyle} data-aos="zoom-in">
        <img
          src="https://lh5.googleusercontent.com/p/AF1QipMIsoHLXdQkjkP69glZkdtS4QBXUsUnJbfB9BRy=w1440-h810-k-no"
          alt="Pizza Palace"
          style={imageStyle}
        />
      </div>
    </div>
  );
};

const containerStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  padding: '50px',
};

const contentStyle = {
  flex: 1,
  textAlign: 'left', // Align text to the left
  marginRight: '50px',
};

const headingStyle = {
  fontSize: '68px',
  fontWeight: 'bold', 
  color: 'red', 
  marginBottom: '20px',
};

const paragraphStyle = {
  fontSize: '18px',
  marginBottom: '20px',
};

const imageContainerStyle = {
  flex: 1,
  textAlign: 'center',
};

const imageStyle = {
  width: '100%',
  maxHeight: '800px',
  borderRadius: '10px',
  boxShadow: '0 0 10px rgba(0, 0, 0, 0.3)',
}
export default AboutUsScreen;